.header {
	.header-mobile {
		background-color: $green;
		&__wrapper {
			display: flex;
			align-items: center;
			justify-content: space-between;
			padding: 26px 55px; }
		&__menu-button-close {
			background: none;
			width: 37px;
			height: 37px;
			margin: 0 13px;
			img {
				width: 100%;
				height: 100%; } }
		&__menu {
			display: flex;
			align-items: center;
			justify-content: center; }
		&__menu-text {
			display: block;
			color: $grey;
			font-family: $SFUIDisplay;
			font-size: 10px;
			font-weight: 700;
			text-transform: uppercase;
			letter-spacing: 1px;
			&.let-spac {
				letter-spacing: 2.5px; } }
		&__length {
			display: flex;
			align-items: center;
			justify-content: center; }
		&__length-text {
			display: block;
			color: $grey;
			font-family: $SFUIDisplay;
			font-size: 10px;
			font-weight: 700;
			text-transform: uppercase;
			letter-spacing: 1px; }
		&__length-ru {
			display: flex;
			align-items: center;
			justify-content: center;
			margin-right: 26px;
			position: relative;
			border-radius: 50%;
			cursor: pointer;
			width: 40px;
			height: 40px;
			transition: background 0.3s;
			&::after {
				display: block;
				position: absolute;
				content: '';
				width: 59px;
				height: 1px;
				top: 50%;
				left: 10%;
				background-color: $dark; }
			&:hover {
				&::after {
					display: none; } } }
		&__length-en {
			display: flex;
			align-items: center;
			justify-content: center;
			margin-left: 26px;
			position: relative;
			border-radius: 50%;
			cursor: pointer;
			width: 40px;
			height: 40px;
			background-color: #fff;
			transition: background 0.3s;
			&::after {
				display: block;
				position: absolute;
				content: '';
				width: 59px;
				height: 1px;
				top: 50%;
				right: 18%;
				background-color: $dark;
				display: none; }
			&:hover {
				&::after {
					display: none; } } }
		&__navigation {
			display: flex;
			flex-direction: column;
			align-items: center;
			justify-content: center;
			padding: 0 55px; }
		&__item {
			background-color: $green;
			width: 100%;
			text-align: center;
			line-height: 84px;
			&.disp-768 {
				display: flex;
				align-items: center;
				justify-content: center; }
			&.border-first {
				width: 100%;
				border-top: 6px solid #ffffff; }
			&.border-two {
				width: 100%;
				border-top: 5px solid #ffffff; }
			&.border-third {
				width: 100%;
				border-top: 4px solid #ffffff; }
			&.border-four {
				width: 100%;
				border-top: 3px solid #ffffff; }
			&.border-five {
				width: 100%;
				border-top: 2px solid #ffffff; }
			&.border-sex {
				width: 100%;
				border-top: 1px solid #ffffff; } }
		&__link {
			color: $grey;
			font-family: $SFUIDisplay;
			font-size: 10px;
			font-weight: 700;
			text-transform: uppercase;
			letter-spacing: 1px;
			text-decoration: none;
			margin-left: -28px;
			border-bottom: 1px solid $green;
			transition: border 0.3s;
			&:hover {
				border-bottom: 1px solid $grey; } } }

	&__wrapper {
		display: flex;
		align-items: center;
		justify-content: space-between;
		padding: 26px 55px;
		background-color: #fff; }
	&__menu {
		display: flex;
		align-items: center;
		justify-content: center; }
	&__menu-text {
		display: block;
		color: $grey;
		font-family: $SFUIDisplay;
		font-size: 10px;
		font-weight: 700;
		text-transform: uppercase;
		letter-spacing: 1px; }
	&__menu-button {
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		background: none;
		width: 50px;
		height: 25px;
		margin: 0 13px;
		.button-line-up {
			display: block;
			height: 1px;
			width: 50px;
			background-color: $dark; }
		.button-line-center {
			display: block;
			height: 2px;
			width: 50px;
			background-color: $dark; }
		.button-line-bottom {
			display: block;
			height: 3px;
			width: 50px;
			background-color: $dark; }
		&:hover {
			justify-content: space-around;
			.button-line-center {
				display: none; } } }
	&__length {
		display: flex;
		align-items: center;
		justify-content: center; }
	&__length-text {
		display: block;
		color: $grey;
		font-family: $SFUIDisplay;
		font-size: 10px;
		font-weight: 700;
		text-transform: uppercase;
		letter-spacing: 1px; }
	&__length-ru {
		display: flex;
		align-items: center;
		justify-content: center;
		margin-right: 26px;
		position: relative;
		border-radius: 50%;
		cursor: pointer;
		width: 40px;
		height: 40px;
		transition: background 0.3s;
		&::after {
			display: block;
			position: absolute;
			content: '';
			width: 59px;
			height: 1px;
			top: 50%;
			left: 10%;
			background-color: $dark; }
		&:hover {
			&::after {
				display: none; } }
		&.hover {
			background-color: #fff;
			&::after {
				display: block; } } }
	&__length-en {
		display: flex;
		align-items: center;
		justify-content: center;
		margin-left: 26px;
		position: relative;
		border-radius: 50%;
		cursor: pointer;
		width: 40px;
		height: 40px;
		background-color: $green;
		transition: background 0.3s;
		&::after {
			display: block;
			position: absolute;
			content: '';
			width: 59px;
			height: 1px;
			top: 50%;
			right: 18%;
			background-color: $dark;
			display: none; }
		&:hover {
			&::after {
				display: none; } }
		&.hover {
			background-color: #fff;
			&::after {
				display: block; } } } }
